'use client';

import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Loader2, Send } from 'lucide-react';

const TemasConsulta = [
  'SEGUIMIENTO DE EXPEDIENTE',
  'CONSULTA POR SUBSANACIÓN DE OBSERVACIONES CON OFICIO',
  'CONSULTA PARA APERTURA DE BOTICA',
  'CONSULTA DE LLENADO DE FORMATOS TUPA',
  'CONSULTA DE LLENADO DE FORMATOS NO TUPA',
  'CONSULTA TÉCNICA DE CROQUIS DEL EE.FF',
  'CONSULTA DEL OBSERVATORIO DE PRECIOS',
  'CONSULTA POR OFICIO DE COMUNICACIÓN',
  'CONSULTA EN QUÉ ESTADO SE ENCUENTRA MI EXPEDIENTE',
  'OTROS'
];

const Cargos = ['Propietario', 'Representante Legal', 'Director Técnico', 'Q.F. Asistente', 'Otros'];

const formSchema = z.object({
  nombreEstablecimiento: z.string().min(3, "El nombre debe tener al menos 3 caracteres."),
  codigoEstablecimiento: z.string().optional(),
  ruc: z.string().optional(),
  emailEstablecimiento: z.string().email("Debe ser un correo electrónico válido."),
  cargo: z.enum(Cargos),
  tema: z.enum(TemasConsulta),
  descripcion: z.string().optional(),
}).refine(data => {
  if (data.tema === 'OTROS') {
    return !!data.descripcion && data.descripcion.length >= 10;
  }
  return true;
}, {
  message: "La descripción es obligatoria y debe tener al menos 10 caracteres cuando el tema es 'Otros'.",
  path: ["descripcion"],
});

export default function NewQueryForm({ onNewQuery, onQuerySuccess }) {
  const [isLoading, setIsLoading] = React.useState(false);

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      nombreEstablecimiento: "",
      codigoEstablecimiento: "",
      ruc: "",
      emailEstablecimiento: "",
      cargo: undefined,
      tema: undefined,
      descripcion: "",
    },
  });

  const temaValue = form.watch("tema");

  async function onSubmit(values) {
    setIsLoading(true);
    const success = await onNewQuery(values);
    setIsLoading(false);
    if (success) {
      form.reset();
      onQuerySuccess(true);
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField control={form.control} name="nombreEstablecimiento" render={({ field }) => ( <FormItem><FormLabel>Nombre del Establecimiento</FormLabel><FormControl><Input placeholder="Ej: Botica Mi Farma" {...field} /></FormControl><FormMessage /></FormItem> )} />
          <FormField control={form.control} name="codigoEstablecimiento" render={({ field }) => ( <FormItem><FormLabel>Código (Opcional)</FormLabel><FormControl><Input placeholder="Ej: 12345" {...field} /></FormControl><FormMessage /></FormItem> )} />
          <FormField control={form.control} name="ruc" render={({ field }) => ( <FormItem><FormLabel>RUC (Opcional)</FormLabel><FormControl><Input placeholder="Ej: 20123456789" {...field} /></FormControl><FormMessage /></FormItem> )} />
          <FormField control={form.control} name="emailEstablecimiento" render={({ field }) => ( <FormItem><FormLabel>Email del Establecimiento</FormLabel><FormControl><Input type="email" placeholder="contacto@establecimiento.com" {...field} /></FormControl><FormMessage /></FormItem> )} />
          <FormField control={form.control} name="cargo" render={({ field }) => ( <FormItem><FormLabel>Cargo que ocupa</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}><FormControl><SelectTrigger><SelectValue placeholder="Seleccione su cargo" /></SelectTrigger></FormControl><SelectContent>{Cargos.map(cargo => ( <SelectItem key={cargo} value={cargo}>{cargo}</SelectItem> ))}</SelectContent></Select><FormMessage /></FormItem> )} />
          <FormField control={form.control} name="tema" render={({ field }) => ( <FormItem><FormLabel>Tema de la Consulta</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}><FormControl><SelectTrigger><SelectValue placeholder="Seleccione un tema" /></SelectTrigger></FormControl><SelectContent>{TemasConsulta.map(tema => ( <SelectItem key={tema} value={tema}>{tema}</SelectItem> ))}</SelectContent></Select><FormMessage /></FormItem> )} />

          {temaValue === 'OTROS' && (
            <div className="md:col-span-2">
              <FormField control={form.control} name="descripcion" render={({ field }) => ( <FormItem><FormLabel>Descripción de la Consulta</FormLabel><FormControl><Textarea placeholder="Describa su consulta en detalle..." className="resize-none" rows={5} {...field} /></FormControl><FormDescription>Sea lo más detallado posible.</FormDescription><FormMessage /></FormItem> )} />
            </div>
          )}
        </div>
        <Button type="submit" disabled={isLoading} className="w-full">
          {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Send className="mr-2 h-4 w-4" />}Enviar Consulta
        </Button>
      </form>
    </Form>
  );
}
