'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Checkbox } from '@/components/ui/checkbox';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Loader2, UserPlus } from 'lucide-react';

const formSchema = z.object({
  nombres: z.string().min(2, "El nombre es requerido."),
  apellidos: z.string().min(2, "El apellido es requerido."),
  colegiatura: z.string().optional(),
  email: z.string().email("Debe ser un correo electrónico válido."),
  celular: z.string().regex(/^[0-9]{9}$/, "El celular debe tener 9 dígitos."),
  autorizaNotificaciones: z.boolean().default(false),
});

export default function RegisterForm({ dni, onSubmit, isLoading }) {
  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      nombres: '',
      apellidos: '',
      colegiatura: '',
      email: '',
      celular: '',
      autorizaNotificaciones: true,
    },
  });

  return (
    <Card className="w-full max-w-lg mx-auto">
      <CardHeader className="text-center">
        <CardTitle className="text-2xl font-bold">Complete su Registro</CardTitle>
        <CardDescription>Por favor, ingrese sus datos para crear su cuenta.</CardDescription>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="dni"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>DNI</FormLabel>
                  <FormControl>
                    <Input {...field} value={dni} disabled />
                  </FormControl>
                </FormItem>
              )}
            />

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <FormField control={form.control} name="nombres" render={({ field }) => ( <FormItem><FormLabel>Nombres</FormLabel><FormControl><Input placeholder="Sus nombres" {...field} /></FormControl><FormMessage /></FormItem> )} />
              <FormField control={form.control} name="apellidos" render={({ field }) => ( <FormItem><FormLabel>Apellidos</FormLabel><FormControl><Input placeholder="Sus apellidos" {...field} /></FormControl><FormMessage /></FormItem> )} />
              <FormField control={form.control} name="email" render={({ field }) => ( <FormItem><FormLabel>Correo Electrónico</FormLabel><FormControl><Input type="email" placeholder="su.correo@ejemplo.com" {...field} /></FormControl><FormMessage /></FormItem> )} />
              <FormField control={form.control} name="celular" render={({ field }) => ( <FormItem><FormLabel>Celular</FormLabel><FormControl><Input placeholder="987654321" {...field} /></FormControl><FormMessage /></FormItem> )} />
            </div>

            <FormField
              control={form.control}
              name="autorizaNotificaciones"
              render={({ field }) => (
                <FormItem className="flex flex-row items-start space-x-3 space-y-0 rounded-md border p-4 shadow">
                  <FormControl>
                    <Checkbox checked={field.value} onCheckedChange={field.onChange} />
                  </FormControl>
                  <div className="space-y-1 leading-none">
                    <FormLabel>¿Autoriza recibir notificaciones por correo sobre sus consultas?</FormLabel>
                  </div>
                </FormItem>
              )}
            />

            <Button type="submit" disabled={isLoading} className="w-full">
              {isLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <UserPlus className="mr-2 h-4 w-4" />
              )}
              Registrarme
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}
