'use client';

import * as React from 'react';
import { collection, query, where, onSnapshot, orderBy } from "firebase/firestore";
import { useFirestore } from '@/firebase';
import { getStatusStyles } from '@/lib/utils';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { CircleHelp } from 'lucide-react';

export default function QueryHistory({ userId }) {
  const firestore = useFirestore();
  const [queries, setQueries] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    if (!firestore || !userId) return;

    const q = query(
      collection(firestore, "queries"),
      where("userId", "==", userId),
      orderBy("fechaCreacion", "desc")
    );

    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const userQueries = [];
      querySnapshot.forEach((doc) => {
        userQueries.push({ id: doc.id, ...doc.data() });
      });
      setQueries(userQueries);
      setLoading(false);
    }, (error) => {
      console.error("Error al obtener el historial de consultas: ", error);
      setLoading(false);
    });

    // Limpiar el listener al desmontar el componente.
    return () => unsubscribe();
  }, [firestore, userId]);

  if (loading) {
    return (
      <div className="space-y-4">
        {[...Array(3)].map((_, i) => (
          <Skeleton key={i} className="h-12 w-full" />
        ))}
      </div>
    );
  }

  if (queries.length === 0) {
    return (
      <Alert>
        <CircleHelp className="h-4 w-4" />
        <AlertDescription>
          Aún no ha realizado ninguna consulta. Puede crear una en la pestaña "Nueva Consulta".
        </AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="border rounded-md">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>N° Expediente</TableHead>
            <TableHead>Tema</TableHead>
            <TableHead>Fecha</TableHead>
            <TableHead className="text-right">Estado</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {queries.map((query) => (
            <TableRow key={query.id}>
              <TableCell className="font-medium">{query.expediente}</TableCell>
              <TableCell>{query.tema}</TableCell>
              <TableCell>{new Date(query.fechaCreacion).toLocaleDateString()}</TableCell>
              <TableCell className="text-right">
                <Badge style={getStatusStyles(query.status)}>{query.status}</Badge>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
