'use client';

import * as React from 'react';
import NewQueryForm from './new-query-form';
import QueryHistory from './query-history';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { User as UserIcon, List, PlusCircle } from 'lucide-react';

export function UserDashboard({ user, onNewQuery }) {
  const [activeTab, setActiveTab] = React.useState("history");

  const handleQuerySuccess = (success) => {
    if (success) {
      // Si la nueva consulta se creó con éxito, cambia a la pestaña de historial.
      setActiveTab("history");
    }
  };

  return (
    <div className="w-full space-y-8">
      <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
        <div className="flex items-center gap-4">
          <div className="bg-primary text-primary-foreground rounded-full h-12 w-12 flex items-center justify-center">
            <UserIcon className="h-6 w-6" />
          </div>
          <div>
            <h1 className="text-2xl font-bold font-headline">Bienvenido, {user.nombres} {user.apellidos}</h1>
            <p className="text-muted-foreground">Aquí puede realizar nuevas consultas y ver su historial.</p>
          </div>
        </div>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="history">
            <List className="mr-2 h-4 w-4" />
            Historial de Consultas
          </TabsTrigger>
          <TabsTrigger value="new">
            <PlusCircle className="mr-2 h-4 w-4" />
            Nueva Consulta
          </TabsTrigger>
        </TabsList>
        <TabsContent value="history">
          <Card>
            <CardHeader>
              <CardTitle>Su Historial de Consultas</CardTitle>
              <CardDescription>A continuación se muestran todas las consultas que ha realizado.</CardDescription>
            </CardHeader>
            <CardContent>
              <QueryHistory userId={user.id} />
            </CardContent>
          </Card>
        </TabsContent>
        <TabsContent value="new">
          <Card>
            <CardHeader>
              <CardTitle>Registrar Nueva Consulta</CardTitle>
              <CardDescription>Complete el formulario para enviar una nueva consulta técnica.</CardDescription>
            </CardHeader>
            <CardContent>
              <NewQueryForm onNewQuery={onNewQuery} onQuerySuccess={handleQuerySuccess} />
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
